import React from 'react';


class Tasklist_Form extends React.Component {
    constructor(){
        super();
    }
    
    render() {
        return(
            <div>
                <div id="customerAction">
                <h1>Customer data</h1>
                <label>Company Name *</label>
                <input id="company" type="text" />
                <label>Customer Name *</label>
                <input id="name" type="text" />
                <label>Age *</label>
                <input id="age" type="text" />
                <br />
                <br />
                <button type="button" value="Add New" ></button>
                <div id="editarea" >
                    <button type="button" id="update" value="" >Update</button>
                    <button type="button" id="cancel" value="">Cancel</button>
                </div>
                <br />
                <div id="display"></div>
                </div>
            </div>
        );
    }
}

export default Tasklist_Form;